import React, { useState } from "react";

/* REACT BOOTSTRAP */
import { Button, Form } from "react-bootstrap";

/* COMPONENTS */
import FormContainer from "../components/FormContainer";
import CheckoutSteps from "../components/CheckoutSteps";

/* REACT - REDUX */
import { useDispatch, useSelector } from "react-redux";

/* ACTION CREATORS */
import { saveShippingAddress } from "../redux/slices/cartSlice";

function ShippingScreen({ history }) {
  // PULLING OUT SHIPPING ADDRESS FROM CART
  const cart = useSelector((state) => state.cart);

  const { shippingAddress } = cart;

  // STATE
  const [address, setAddress] = useState(shippingAddress.address);
  const [city, setCity] = useState(shippingAddress.city);
  const [postalCode, setPostalCode] = useState(shippingAddress.postalCode);
  const [country, setCountry] = useState(shippingAddress.country);
  const [addressError, setAddressError] = useState('');
  const [cityError, setCityError] = useState('');
  const [postalCodeError, setPostalCodeError] = useState('');
  const [countryError, setCountryError] = useState('');

  const dispatch = useDispatch();

  // VALIDATION FUNCTIONS
  const validateAddress = (addr) => {
    const isNumberOnly = /^[0-9]+$/;
    return !isNumberOnly.test(addr);
  };

  const validateCity = (cty) => {
    const isNumberOnly = /^[0-9]+$/;
    return cty.length <= 10 && !isNumberOnly.test(cty);
  };

  const validatePostalCode = (code) => {
    const postalCodePattern = /^[0-9]{1,6}$/;
    return postalCodePattern.test(code);
  };

  const validateCountry = (ctr) => {
    const isAlphaNumeric = /^[a-zA-Z]{1,10}$/;
    return isAlphaNumeric.test(ctr);
  };

  const submitHandler = (e) => {
    e.preventDefault();

    if (!validateAddress(address)) {
      setAddressError('Address must not be numbers only');
      return;
    }

    if (!validateCity(city)) {
      setCityError('City must be a string and maximum 10 characters');
      return;
    }

    if (!validatePostalCode(postalCode)) {
      setPostalCodeError('Postal code must be a number with a maximum of 6 digits');
      return;
    }

     if (!validateCountry(country)) {
      setCountryError('Country must be alphabets (A-Z,a-z) only with a maximum of 10 characters');
      return;
    }

    /* FIRING OFF THE ACTION CREATORS USING DISPATCH TO SAVE ADDRESS */
    dispatch(
      saveShippingAddress({
        address,
        city,
        postalCode,
        country,
      })
    );

    // PUSHING USER TO PAYMENTS PAGE AFTER SAVING ADDRESS
    history.push("./payment");
  };

  return (
    <FormContainer>
      <CheckoutSteps step1 step2 />

      <h1>Shipping</h1>
      <Form onSubmit={submitHandler}>
        <Form.Group controlId="address">
          <Form.Label>Address</Form.Label>
          <Form.Control
            required
            type="text"
            placeholder="Enter Address"
            value={address ? address : ""}
            onChange={(e) => {
              setAddress(e.target.value);
              setAddressError('');
            }}
            isInvalid={!!addressError}
          />
          <Form.Control.Feedback type="invalid">
            {addressError}
          </Form.Control.Feedback>
        </Form.Group>

        <Form.Group controlId="city">
          <Form.Label>City</Form.Label>
          <Form.Control
            required
            type="text"
            placeholder="Enter City"
            value={city ? city : ""}
            onChange={(e) => {
              setCity(e.target.value);
              setCityError('');
            }}
             isInvalid={!!cityError}
          />
           <Form.Control.Feedback type="invalid">
             {cityError}
           </Form.Control.Feedback>
        </Form.Group>

        <Form.Group controlId="postalCode">
          <Form.Label>Postal Code</Form.Label>
          <Form.Control
            required
            type="text"
            placeholder="Enter Postal Code"
            value={postalCode ? postalCode : ""}
            onChange={(e) => {
              setPostalCode(e.target.value);
              setPostalCodeError('');
            }}
             isInvalid={!!postalCodeError}
          />
           <Form.Control.Feedback type="invalid">
             {postalCodeError}
           </Form.Control.Feedback>
        </Form.Group>

        <Form.Group controlId="country">
          <Form.Label>Country</Form.Label>
          <Form.Control
            required
            type="text"
            placeholder="Enter Country"
            value={country ? country : ""}
            onChange={(e) => {
              setCountry(e.target.value);
              setCountryError('');
            }}
             isInvalid={!!countryError}
          />
           <Form.Control.Feedback type="invalid">
             {countryError}
           </Form.Control.Feedback>
        </Form.Group>

        <Button className="my-3" type="submit" variant="primary">
          Continue
        </Button>
      </Form>
    </FormContainer>
  );
}

export default ShippingScreen;
